@import '../../../index';

.poster{
  @include flex-column;
  gap: 20px;
  grid-column: 1;
  .img-box{
    width: 100%;
    position: relative;
    border-radius: $border-radius-m;
    overflow: hidden;
    box-shadow: var(--color-main) 0 0 5px 2px;
    min-height: 400px;
    height: 100%;
    @media (max-width: 940px){
      border-radius: 0;
      box-shadow: black 0 0 4px 2px;
    }
    @media (max-width: 500px){
      min-height: auto;
      height: 250px;
    }

    img{
      display: block;
      position: absolute;
      height: 100%;
      width: 100%;
      inset: 0;
      object-fit: cover;
    }
    .poster{
      object-position: top;
    }
  }
}