@import '../../../index';

.btn{
  position: relative;
  padding: 10px 25px;
  max-width: 200px;
  min-width: max-content;
  min-height: 40px;
  border:none;
  border-radius: $border-radius-l;
  text-transform: uppercase;
  transition: var(--transition);
 &.primary{
   background: linear-gradient(275deg, $primary-color 6%, rgb(29, 105, 190) 40%, rgb(24, 98, 169) 71%, rgb(67, 99, 222) 97%);
   color: white;
   &:hover,
   &:focus-visible{
     background-image: linear-gradient(#1c4d1c 0%, $effect-color 55%,#1c4d1c 95%);
     box-shadow: $effect-color 0 0 2px 2px inset;
     transform: scale(1.1);
   }
   &:active{
     animation: pulse 0.3s;
     box-shadow: 0 0 0 2em rgba(#fff,0);
   }
 }
  @keyframes pulse {
    0% { box-shadow: 0 0 0 0 $effect-color; }
  }
  @media(max-width: 480px) {
    width: 90%;
  }
  &.silver {
    position: relative;
    top:0;
    left:0;
    max-width: max-content;
    margin-top: 20px;
    padding: 10px 30px;
    background: linear-gradient(0deg, rgb(70, 68, 68) 10%, rgb(59 58 58) 30%, rgb(2 1 1) 50%, rgb(112 111 111) 70%, rgb(70, 68, 68) 90%);
    border: 4px solid #222;
    border-radius: 25px;
    font-size: 1.1rem;
    text-transform: uppercase;
    transition: var(--transition);
    color:white;
    overflow: hidden;
    &:hover,
    &:focus-visible{
      box-shadow: inset 0 0 3px 3px #0f59f3;
      background: linear-gradient(0deg, rgb(70, 68, 68) 10%, rgb(96, 94, 94) 60%, rgb(70, 68, 68) 90%);
    }
    &:active{
      box-shadow: inset 0 0 0 4px #000000;
      background: linear-gradient(0deg, rgb(70, 68, 68) 10%, rgb(96, 94, 94) 40%, rgb(99 95 95) 50%, rgb(96, 94, 94) 60%, rgb(70, 68, 68) 90%);
    }
  }
  &.order-button{
    background: linear-gradient(to right, #5fa3a6, #83a4d4);
    box-shadow: var(--bg-body-dark) 0px 0px 4px 2px;
    &:hover,
    &:focus-visible{
      background: linear-gradient(to right,  $primary-color, #0d55bb);
      color:white;
    }
    &:active{
      animation: pulse 0.3s;
      box-shadow: 0 0 0 2em rgba(#fff,0);
    }
    @keyframes pulse {
      0% { box-shadow: 0 0 0 0 $primary-color; }
    }
    @media(max-width: 480px) {
      width: 90%;
    }
  }
}
