@import '../../index';

.main {
  position: relative;
  flex-grow: 1;
  padding: 20px 0 20px;
  .wrapper {
    width: 80%;
    height: 40%;
    background-color: var(--color-main);
    font-size: 24px;
    margin: 20px auto;
    padding: 20px 10px;
    border-radius: 15px;

    & * {
      text-align: center;
      color: var(--color-secondary);
    }

    strong {
      .status {
        font-size: 36px;
      }
    }
  }

}